import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../logicMainteTab";
import LogicElementUtil from "../util/logicElementUtil";
import LogicUtil from "../util/logicUtil";
import ParamUtil from "../util/paramUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetVisualMng = () => {
    const { node, setFocusNode, invalidate, setModify, isModify } = useContext(FactoryTreeContext);
    const mng = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;

    const [isBlankMethod, hasDisappearParam, hasShrinkParam] = useMemo(() => {
        const list = node.children.map(el => {
            return ((el.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamItem).name;
        });
        return [
            list.length === 0,
            list.includes(ParamUtil.EffectParamType.DISAPPEAR_START),
            list.includes(ParamUtil.EffectParamType.SHRINK_START)
        ]
    }, [node]);

    /**
     * 
     */
    const toggleElements = (hasNode: boolean, listPos: number, paramElements: LogicUtil.NodeParamItem[]) => {
        const size = paramElements.length;
        if (hasNode) {
            LogicElementUtil.removeChild(node, listPos, size);
            setFocusNode({ ...node });
            invalidate();
        } else {
            paramElements.forEach((data, i) => {
                const wrap: LogicUtil.WrapElement = {
                    type: 'param-item',
                    data
                };
                LogicElementUtil.insertChild(wrap, node, listPos + i);
            });
            setFocusNode({ ...node });
            invalidate();
        }
    };

    const addElements = (paramElements: LogicUtil.NodeParamItem[]) => {
        const size = paramElements.length;
        paramElements.forEach((data, i) => {
            const wrap: LogicUtil.WrapElement = {
                type: 'param-item',
                data
            };
            LogicElementUtil.insertChild(wrap, node, i);
        });
        setFocusNode({ ...node });
        invalidate();
    };

    return (<>
        <FormUtil.BorderPanel
            title="method"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Blank Method'}
                            width={300}
                            isRelation
                            isUse={isBlankMethod}
                            callback={() => {
                                mng.params.length = 0;
                                node.children.length = 0;
                                setFocusNode({ ...node });
                                invalidate();
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Particle Method'}
                            width={300}
                            isRelation
                            isUse={!isBlankMethod}
                            callback={() => {
                                addElements([
                                    {
                                        name: ParamUtil.VisualParamType.RADIUS,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '20' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_R,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_G,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_B,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                    {
                                        name: ParamUtil.VisualParamType.COLOR_A,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '255' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="effect"
            isEnable={!isModify && !isBlankMethod}
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Disappear'}
                            width={300}
                            isUse={hasDisappearParam}
                            callback={() => {
                                toggleElements(hasDisappearParam, 5, [
                                    {
                                        name: ParamUtil.EffectParamType.DISAPPEAR_START,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
                <FormUtil.Record
                    textAlign="center"
                    jsx={
                        <FormUtil.Switch
                            label={'Shrink'}
                            width={300}
                            isUse={hasShrinkParam}
                            callback={() => {
                                toggleElements(hasShrinkParam, !hasDisappearParam ? 5 : 6, [
                                    {
                                        name: ParamUtil.EffectParamType.SHRINK_START,
                                        formulaItems: ['var'], values: [{ type: "direct", value: '0' }]
                                    },
                                ]);
                            }}
                        />
                    }
                />
            </>}
        />
    </>);
}

export default FactDetVisualMng;