import { assert } from "console";
import TreeUtil from "../../../utils/treeUtil";
import DrawUtil from "../../graphic/drawUtil";
import Particle from "../../graphic/particle";
import VectorUtil from "../../graphic/vectorUtil";
import LogicUtil from "../factory/util/logicUtil";
import ParamUtil from "../factory/util/paramUtil";

namespace PreviewUtil {

    export const SCREEN_WIDTH = 550;
    export const SCREEN_HEIGHT = 550;

    export type ValueMap = {
        name: string;
        value: number;
    }

    export const buildOriginProps = (
        owner: Particle.Instance[],
        node: TreeUtil.ElementNode,
        rootData: LogicUtil.WrapElement,
        sends: ValueMap[]
    ): Particle.Props => {

        /**
         * 
         * @param name 
         * @returns 
         */
        const searchDependLogic = (name: string): LogicUtil.NodeLogicItem | null => {
            const mng = (rootData.data as LogicUtil.NodeRoot).inheritMng.data as LogicUtil.NodeLogicManager;
            const wrap = mng.defs.find(def => (def.data as LogicUtil.NodeLogicItem).name === name);
            if (wrap == undefined) return null;
            return wrap.data as LogicUtil.NodeLogicItem;
        }

        /**
         * 
         * @param cur 
         * @param sends 
         * @returns 
         */
        const createParticleRec = (cur: LogicUtil.NodeLogicItem, sends: ValueMap[]): Particle.Props => {
            let valueMapList: ValueMap[] = sends.concat(ConstantValueMap);


            // 
            const fieldMng = searchElement('field-mng', cur.mngs);
            if (fieldMng != null) {
                (fieldMng.data as LogicUtil.NodeFieldManager).fields.forEach(field => {
                    const paramItem = field.data as LogicUtil.NodeParamItem;
                    const value = convertFormulaToValue(paramItem, valueMapList);
                    valueMapList.push({ name: paramItem.name, value });
                });
            }

            const visualParams = (searchElement('visual-mng', cur.mngs)?.data as LogicUtil.NodeParamManager).
                params.map(param => param.data as LogicUtil.NodeParamItem);
            const behaviorParams = (searchElement('behavior-mng', cur.mngs)?.data as LogicUtil.NodeParamManager).
                params.map(param => param.data as LogicUtil.NodeParamItem);

            const getVisualParam = (name: string) => {
                return convertParamsToValue(name, visualParams, valueMapList);
            }
            const getBehaviorParam = (name: string) => {
                return convertParamsToValue(name, behaviorParams, valueMapList);
            }

            // Behavior
            const limit = Math.floor(getBehaviorParam(ParamUtil.BehaviorParamType.LIMIT) as number);
            const pos = {
                x: getBehaviorParam(ParamUtil.BehaviorParamType.POS_X) as number,
                y: getBehaviorParam(ParamUtil.BehaviorParamType.POS_Y) as number
            };
            const vec = new VectorUtil.Vector({ x: 0, y: 0 });
            const isVectorMethod = behaviorParams.find(param => param.name === ParamUtil.BehaviorParamType.VEC_X) != undefined;
            if (isVectorMethod) {
                vec.xy.x = getBehaviorParam(ParamUtil.BehaviorParamType.VEC_X) as number;
                vec.xy.y = getBehaviorParam(ParamUtil.BehaviorParamType.VEC_Y) as number;
                vec.syncFromXY();
            } else {
                vec.rad = getBehaviorParam(ParamUtil.BehaviorParamType.VEC_RAD) as number;
                vec.len = getBehaviorParam(ParamUtil.BehaviorParamType.VEC_LEN) as number;
                vec.syncFromRad();
            }

            /**
             * 
             */
            const extendList: ((self: Particle.Instance) => void)[] = [];

            // 
            const connectMng = searchElement('connect-mng', cur.mngs);
            if (connectMng != null) {

                // 
                valueMapList.push({ name: ParamUtil.BehaviorParamType.POS_X, value: pos.x });
                valueMapList.push({ name: ParamUtil.BehaviorParamType.POS_Y, value: pos.y });
                valueMapList.push({ name: ParamUtil.BehaviorParamType.VEC_X, value: vec.xy.x });
                valueMapList.push({ name: ParamUtil.BehaviorParamType.VEC_X, value: vec.xy.y });
                valueMapList.push({ name: ParamUtil.BehaviorParamType.VEC_RAD, value: vec.rad });
                valueMapList.push({ name: ParamUtil.BehaviorParamType.VEC_LEN, value: vec.len });

                const connects = (connectMng.data as LogicUtil.NodeInheritConnectManager).connects;

                connects.forEach(connect => {
                    const connector = connect.data as LogicUtil.NodeInheritConnector;
                    if (connector.disable ?? false) return;

                    const logicItem = connector.connectType === 'create' ?
                        searchElement('inherit-cache-item', connector.mngs)?.data as LogicUtil.NodeLogicItem :
                        searchDependLogic(connector.depend);

                    if (logicItem != null) {

                        extendList.push((self: Particle.Instance) => {

                            const addValueMap = (item: ValueMap) => {
                                const map = valueMapList.find(map => map.name === item.name);
                                if (map != undefined) map.value = item.value;
                                else valueMapList.push(item);
                            }
                            addValueMap({ name: ParamUtil.SelfParamType.FRAME, value: self.frame });
                            addValueMap({ name: ParamUtil.SelfParamType.LIMIT, value: self.limit });
                            addValueMap({ name: ParamUtil.SelfParamType.POS_X, value: self.pos.x });
                            addValueMap({ name: ParamUtil.SelfParamType.POS_Y, value: self.pos.y });
                            addValueMap({ name: ParamUtil.SelfParamType.VEC_X, value: self.vec.xy.x });
                            addValueMap({ name: ParamUtil.SelfParamType.VEC_Y, value: self.vec.xy.y });
                            addValueMap({ name: ParamUtil.SelfParamType.VEC_RAD, value: self.vec.rad });
                            addValueMap({ name: ParamUtil.SelfParamType.VEC_LEN, value: self.vec.len });

                            let loopCnt = 1;
                            const extendItem = searchElement('extend-mng', connector.mngs) as LogicUtil.WrapElement;
                            const extendParams = (extendItem.data as LogicUtil.NodeParamManager).params.map(param => param.data as LogicUtil.NodeParamItem);
                            const loopCntVal = convertParamsToValue(ParamUtil.ExtendParamType.LOOP_COUNT, extendParams, valueMapList);

                            // 
                            const isInvoke = convertParamsToBoolean(ParamUtil.ExtendParamType.INVOKE, extendParams, valueMapList) ?? true;

                            // 
                            if (!isInvoke) return;

                            // 
                            if (loopCntVal != undefined) {
                                loopCnt = loopCntVal;
                            }

                            for (let i = 0; i < loopCnt; i++) {
                                // console.log(self.frame);

                                // 
                                const iterateValueMap: ValueMap[] = [
                                    { name: ParamUtil.ExtendParamType.LOOP_COUNT, value: loopCnt },
                                    { name: ParamUtil.ExtendParamType.LOOP_ITERATE, value: i }
                                ];

                                const sendItem = searchElement('send-mng', connector.mngs)?.data as LogicUtil.NodeParamManager;
                                const params = sendItem.params.map(param => param.data as LogicUtil.NodeParamItem);
                                const sendValueMapList: ValueMap[] = params.map(item => {
                                    return { name: item.name, value: convertParamsToValue(item.name, params, valueMapList.concat(iterateValueMap)) ?? 0 }
                                });

                                const props = createParticleRec(
                                    logicItem,
                                    sendValueMapList
                                );
                                self.owner.push(new Particle.Instance(props));
                            }
                        });
                    }
                });
            }
            // 
            const decayLimit = getBehaviorParam(ParamUtil.ModulateParamType.DECAY_LIMIT);
            const decayRate = getBehaviorParam(ParamUtil.ModulateParamType.DECAY_RATE);
            const gravity = getBehaviorParam(ParamUtil.ModulateParamType.GRAVITY_VAL);
            if (decayLimit != undefined) {
                extendList.push((self: Particle.Instance) => {
                    modulate(self, {
                        decayLimit, decayRate: decayRate as number, gravity: gravity as number
                    });
                });
            }

            // Visual
            const radius = getVisualParam(ParamUtil.VisualParamType.RADIUS);
            const colorR = getVisualParam(ParamUtil.VisualParamType.COLOR_R);
            const colorG = getVisualParam(ParamUtil.VisualParamType.COLOR_G);
            const colorB = getVisualParam(ParamUtil.VisualParamType.COLOR_B);
            const colorA = getVisualParam(ParamUtil.VisualParamType.COLOR_A);
            // 
            let disappearStart = getVisualParam(ParamUtil.EffectParamType.DISAPPEAR_START);
            let shrinkStart = getVisualParam(ParamUtil.EffectParamType.SHRINK_START);

            let drawer: (self: Particle.Instance, ctx: CanvasRenderingContext2D) => void = () => { };
            if (radius != undefined) {
                drawer = DrawUtil.createGradDrawer({
                    radius: radius,
                    color: DrawUtil.convertColorCodeFromRgba({
                        r: colorR as number,
                        g: colorG as number,
                        b: colorB as number,
                        a: colorA as number,
                    }),
                    disappearStartRate: disappearStart,
                    shrinkStartRate: shrinkStart
                });
            }

            return {
                owner, pos, vec,
                limit, drawer,
                extend: extendList
            };
        }

        const origin = (node.data as LogicUtil.WrapElement).data as LogicUtil.NodeLogicItem;
        return createParticleRec(origin, sends);
    }

    const searchElement = (type: LogicUtil.NodeType, warpList: LogicUtil.WrapElement[]) => {
        return warpList.find(wrap => wrap.type === type) ?? null;
    }

    /**
     * 
     * @param name 
     * @param params 
     * @param valueMapList 
     * @returns 
     */
    const convertParamsToValue = (name: string, params: LogicUtil.NodeParamItem[], valueMapList: ValueMap[]): number | undefined => {
        const formula = params.find(param => param.name === name);
        if (formula == undefined) return undefined;
        return convertFormulaToValue(formula, valueMapList);
    }

    const convertFormulaToValue = (formula: LogicUtil.NodeParamItem, valueMapList: ValueMap[]): number => {
        const values = formula.values.map(v => {
            if (v.type === 'direct') return Number(v.value);
            const valueMap = valueMapList.find(m => m.name === v.value);
            if (valueMap == undefined) return 0;
            return valueMap.value;
        });

        let formulaStr = '';
        let varCnt = 0;
        formula.formulaItems.forEach((item, i) => {
            let str = item;
            if (item === 'var') {
                str = values[varCnt].toString();
                varCnt++;
            }
            else if (item === 'rand(') str = 'Math.random()*(';
            else if (item === 'integer(') str = 'Math.floor(';
            formulaStr += str;
        });
        // console.log(formulaStr);
        const ret = Function(`return (${formulaStr})`)();
        // console.log(ret);
        return ret;
    }

    const convertParamsToBoolean = (name: string, params: LogicUtil.NodeParamItem[], valueMapList: ValueMap[]): boolean | undefined => {
        const formula = params.find(param => param.name === name);
        if (formula == undefined) return undefined;
        return convertFormulaToBoolean(formula, valueMapList);
    }
    const convertFormulaToBoolean = (formula: LogicUtil.NodeParamItem, valueMapList: ValueMap[]): boolean => {
        const values = formula.values.map(v => {
            if (v.type === 'direct') return Number(v.value);
            const valueMap = valueMapList.find(m => m.name === v.value);
            if (valueMap == undefined) return 0;
            return valueMap.value;
        });

        let formulaStr = '';
        let varCnt = 0;
        formula.formulaItems.forEach((item, i) => {
            let str = item;
            if (item === 'var') {
                str = values[varCnt].toString();
                varCnt++;
            }
            else if (item === 'rand(') str = 'Math.random()*(';
            else if (item === 'integer(') str = 'Math.floor(';
            else if (item === '=') str = '==';
            formulaStr += str;
        });
        // console.log(formulaStr);
        const ret = Function(`return (${formulaStr})`)();
        // console.log(formulaStr);
        return ret;
    }

    export const ConstantValueMap: ValueMap[] = [
        { name: 'screen_width', value: SCREEN_WIDTH },
        { name: 'screen_height', value: SCREEN_HEIGHT },
        { name: 'pi', value: 3.14159265359 },
        { name: 'radian_round', value: 3.14159265359 * 2 }
    ];

    type ModulateParam = {
        decayRate: number;
        decayLimit: number;
        gravity: number;
    }

    const modulate = (self: Particle.Instance, param: ModulateParam) => {

        if (self.vec.len > param.decayLimit) {
            self.vec.len = self.vec.len * param.decayRate;
            self.vec.syncFromRad();
            self.vec.xy.y += param.gravity;
            self.vec.syncFromXY();
        }
    }
}

export default PreviewUtil;