import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import { FactoryTreeContext } from "../logicMainteTab";
import LogicElementUtil from "../util/logicElementUtil";
import LogicUtil from "../util/logicUtil";
import ModifyOperations from "./common/modifyOperations";

const ListName = 'args';
const FactDetArgumentItem = () => {
    const { node, removeSelf, setModify, isModify, invalidate } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    const [name, setName] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [defaultValue, setDefaultValue] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [argType, setArgType] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const [isOrigin, reservedList] = useMemo(() => {
        const type = (node.parent?.parent?.data as LogicUtil.WrapElement).type;
        const isOrigin = type === 'origin-item';

        const reservedList: string[] = [];
        return [isOrigin, reservedList];
    }, [node]);

    /**  */
    const paramNameList = useMemo(() => {
        const owner = (node.parent?.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getParamNameList(owner);
    }, [node]);

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeArgumentItem;
        setName({ ...name, value: param.name });
        setArgType({ ...argType, value: param.valueType });
        if (isOrigin && param.defVal != undefined) {
            setDefaultValue({ ...defaultValue, value: param.defVal.toString() });
        }
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
        } else {
            mappingForm();
        }
    }, [node]);

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            listName={ListName}
            checkableFields={isOrigin ? [name, argType, defaultValue] : [name, argType]}
            firstRegist={() => {
                let defVal = undefined;
                if (isOrigin && defaultValue.value != '') {
                    defVal = Number(defaultValue.value);
                }
                const param: LogicUtil.NodeArgumentItem = {
                    name: name.value,
                    valueType: argType.value as LogicUtil.ArgumentType,
                    defVal
                };
                param.name = name.value;
                wrap.data = param;
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeArgumentItem;
                param.name = name.value;
                param.valueType = argType.value as LogicUtil.ArgumentType;
                param.defVal = undefined;
                if (isOrigin && defaultValue.value != '') {
                    param.defVal = Number(defaultValue.value);
                }
                wrap.data = param;
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Name"
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={name}
                            setCheckable={setName}
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 1, 16),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => {
                                        const list = paramNameList.filter(name => {
                                            // 
                                            if (wrap.data != null) {
                                                return (wrap.data as LogicUtil.NodeArgumentItem).name !== name;
                                            }
                                            return true;
                                        });
                                        return !list.includes(value);
                                    },
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Type"
                    jsx={
                        <FormUtil.Combobox
                            checkable={argType}
                            setCheckable={setArgType}
                            list={LogicUtil.ArgTypes.map(type => {
                                return { value: type, labelText: type, isDisabled: type === 'color' }
                            })}
                            headBlank
                            width={200}
                            isReadOnly={!isModify}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkRequired(value),
                                        errorType: "required"
                                    }
                                ]
                            }
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Default"
                    isVisible={isOrigin}
                    jsx={
                        <FormUtil.TextField
                            width={200}
                            isReadOnly={!isModify}
                            checkable={defaultValue}
                            setCheckable={setDefaultValue}
                            validates={
                                [
                                    {
                                        checker: (value) => ValidateUtil.checkNumber(value),
                                        errorType: "value"
                                    }
                                ]
                            }
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            isEnable={!isModify}
            innerJsx={<>
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                removeSelf(ListName);
                            }
                        }
                    ]}
                />
            </>}
        />
    </>);
}

export default FactDetArgumentItem;